html,
body,
#root {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  width: 100vw;
  height: 100vh;
}

.button-wrapper {
	height: 6rem;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	font-weight: 100;
	font-size: 1.4rem;
	background: #e0e1e6;
	color: #888888;
	-webkit-flex:1 1;
	        flex:1 1;
	outline: 1px solid #888888;
	cursor: pointer;
}
.operator {
	background-color: #fe9241;
	color: #ffffff;
}
.input {
	height: 6rem;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	font-weight: 800;
	font-size: 1.4rem;
	background-color: #000000;
	color: #ffffff;
	padding: .5rem;
	border-bottom: 1px solid #888888;
	width: 96.5%;
}
.clear-button {
	height: 6rem;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	font-weight: 200;
	font-size: 1.4rem;
	background-color: #c5c3cd;
	color: #000000;
	-webkit-flex: 1 1;
	        flex: 1 1;
	outline: 1px solid #888888;
}
.App {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	height: 100vh;
	width: 100vw;
}

.calc-wrapper {
	width: 25rem;
	height: 37.5rem;
}

.row {
	display: -webkit-flex;
	display: flex;
	width: 100%;
}
